/* CSS Style Phụ kiện */
.feetStyle{
  width: 500px;
    height: 1000px;
    position: absolute;
    bottom: -40%;
    right: -3.5%;
    transform: scale(0.5);
    z-index: 1;
}

.handbagStyle{
  width: 500px;
    height: 1000px;
    position: absolute;
    bottom: -40.5%;
    right: -3.5%;
    transform: scale(0.5);
    z-index: 4;
}
  
.hairStyle{
  width: 1000px;
    height: 1000px;
    position: absolute;
    top: -75%;
    right: -58%;
    transform: scale(0.15);
    /* z-index: 4; */
}

.backgroundStyle{
    width: 900px;
    height: 1500px;
    position: absolute;
    bottom: -90%;
    right: -50%;
    transform: scale(0.5);
    z-index: -1;
}

.neckLace {
  width: 250px;
  height: 500px;
